.cards__list {
  display: flex;
  list-style-type: none;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
}

.cards__image {
  width: 200px;
  height: 220px;
}

.cards__headline {
  font-size: 24px;
  text-align: left;
  margin: 0;
  margin-bottom: 40px;
  border-bottom: 2px solid $primary;
}

.cards__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 200px;
  border: 1px solid $primary;
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 9px $hover;
  }

  &:not(:last-child) {
    margin-right: 10px;
  }
}

.cards__title {
  font-size: 18px;
  padding-top: 10px;
  margin: 0;
  margin-bottom: 10px;
}

.cards__skills-wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 5px 5px;
  margin-bottom: 10px;
}

.cards__skills {
  font-size: 13px;
  line-height: 1;
  font-weight: 400;
  background-color: $skills;
  padding: 3px 5px;
  margin-right: 4px;
  margin-bottom: 5px;
  border-radius: 10px;
}

.cards__price {
  margin: 0 auto;
  margin-top: auto;
  margin-bottom: 10px;
  padding: 5px;
  border: 2px solid $skills;
  width: 100px;
  border-radius: 20px;
}

.cards__button {
  padding: 10px 10px;
  width: 250px;
  background-color: $skills;
  color: $secondary;
  border: none;
  border-radius: 20px;
  text-transform: uppercase;
  margin-bottom: 10px;
  cursor: pointer;

  &:hover {
    background-color: $hover;
  }
}
