@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

.page__body-container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  min-height: 100vh;
}

.main-header {
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  margin-bottom: 50px;
}

.main-header__link {
  display: block;
  font-size: 18px;
  text-decoration: none;
  color: #77cbec;
  padding: 10px 10px;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 400;
}
.main-header__link--logo {
  font-weight: 700;
  font-size: 22px;
}
.main-header__link:hover {
  color: #566857;
  border-bottom: 1px solid #566857;
  padding-bottom: 9px;
}

.main-header__list {
  display: flex;
  list-style-type: none;
  margin: 0;
}

.main {
  flex: 1;
  padding: 0 50px;
}

.cards__list {
  display: flex;
  list-style-type: none;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
}

.cards__image {
  width: 200px;
  height: 220px;
}

.cards__headline {
  font-size: 24px;
  text-align: left;
  margin: 0;
  margin-bottom: 40px;
  border-bottom: 2px solid #000000;
}

.cards__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 200px;
  border: 1px solid #000000;
  cursor: pointer;
}
.cards__item:hover {
  box-shadow: 0 0 9px #566857;
}
.cards__item:not(:last-child) {
  margin-right: 10px;
}

.cards__title {
  font-size: 18px;
  padding-top: 10px;
  margin: 0;
  margin-bottom: 10px;
}

.cards__skills-wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 5px 5px;
  margin-bottom: 10px;
}

.cards__skills {
  font-size: 13px;
  line-height: 1;
  font-weight: 400;
  background-color: #77cbec;
  padding: 3px 5px;
  margin-right: 4px;
  margin-bottom: 5px;
  border-radius: 10px;
}

.cards__price {
  margin: 0 auto;
  margin-top: auto;
  margin-bottom: 10px;
  padding: 5px;
  border: 2px solid #77cbec;
  width: 100px;
  border-radius: 20px;
}

.cards__button {
  padding: 10px 10px;
  width: 250px;
  background-color: #77cbec;
  color: #ffffff;
  border: none;
  border-radius: 20px;
  text-transform: uppercase;
  margin-bottom: 10px;
  cursor: pointer;
}
.cards__button:hover {
  background-color: #566857;
}

.main-footer {
  display: flex;
  justify-content: space-between;
  padding: 10px 50px;
}

.main-footer__link {
  font-size: 18px;
  color: #77cbec;
  text-decoration: none;
}
.main-footer__link:hover {
  color: #566857;
}

.social__list {
  display: flex;
  list-style-type: none;
  margin: 0;
  min-width: 250px;
  padding: 0;
}

.social__item:not(:last-child) {
  margin-right: 30px;
}

.social__link {
  display: block;
  width: 25px;
  height: 25px;
  position: relative;
}

.social__icon {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  fill: #77cbec;
  cursor: pointer;
}
.social__icon:hover {
  fill: #566857;
}

.modal-container {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 2;
  cursor: pointer;
}

.modal-container--closed {
  display: none;
}

.modal-container--opened {
  display: flex;
}

.modal {
  background-color: #ffffff;
  margin: auto;
  padding: 28px 30px 46px 30px;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  z-index: 3;
}

.modal__title {
  margin: 0 auto;
  margin-bottom: 22px;
  font-size: 25px;
  line-height: 1.2;
  width: 160px;
  text-transform: uppercase;
  text-align: left;
  padding: 0;
}

.form {
  border: none;
  padding: 0;
  margin: 0;
}

.form__fieldset {
  margin-bottom: 10px;
}

.form__list {
  display: flex;
  justify-content: center;
  list-style-type: none;
  font-size: 17px;
  line-height: 0.94;
  margin: 0 auto;
  margin-bottom: 31px;
  padding: 0;
}

.form__item {
  position: relative;
  width: 82px;
  height: 52px;
}

.form__item:not(:last-child) {
  margin-right: 8px;
}

.form__input {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 82px;
  height: 52px;
}

.form__mark {
  position: absolute;
  display: block;
  width: 80px;
  height: 50px;
  border: 2px solid #77cbec;
  cursor: pointer;
}

.form__input[type=radio]:checked + .form__mark {
  border: 2px solid #566857;
}

.form__legend {
  display: inline-block;
  font-size: 15px;
  line-height: 1.1;
  margin-bottom: 28px;
  font-weight: 400;
}

.form__title {
  display: inline-block;
  font-size: 15px;
  line-height: 1.1;
  margin-bottom: 22px;
  padding-top: 18px;
  padding-bottom: 18px;
  font-weight: 400;
}

.form__input-text {
  margin-bottom: 10px;
}

.form__button {
  padding: 10px 10px;
  width: 250px;
  background-color: #77cbec;
  color: #ffffff;
  border: none;
  border-radius: 20px;
  text-transform: uppercase;
  margin-bottom: 10px;
  cursor: pointer;
}
.form__button:hover {
  background-color: #566857;
}